import firebase from 'firebase'

export const addMarcadorNoBancoSoa = (uid, coordinates) => {
    firebase.database().ref('geoLocations/' + uid).set({ coordinates })
}

export const pegaCordenadasSalvasNoBanco = async () => {
    let coordinates = []
    return firebase.database().ref('geoLocations').once('value', (snapshot) => {
        if (snapshot.val())
            Object.keys(snapshot.val()).map((key) =>
                coordinates.push(snapshot.val()[key].coordinates))
    }).then(() => coordinates)
}

export const temUsuarioLogado = async (callback) => {
    const user = firebase.auth().currentUser
    callback(user)
}

function testeHttpsBasico() {
    return new Promise((resolve, reject) => {
        const Http = new XMLHttpRequest();
        const url = 'http://localhost:5000/lutre-3c536/us-central1/httpsbasico';
        Http.onload = (e) => {
            resolve(Http.responseText)
        }
        Http.onerror = (e) => {
            reject(e)
        }
        Http.open("GET", url);
        Http.send();
    })
}
export default testeHttpsBasico


